div.server-inactive {
    width: fit-content;
    border: thin solid black;
    font-weight: bold;
    padding: 5pt;
    background-color: indianred;
    border-radius: 5pt;
    margin-left: auto;
    margin-right: auto;
}

div.server-active {
    width: fit-content;
    border: thin solid black;
    font-weight: bold;
    padding: 5pt;
    background-color: lightgreen;
    border-radius: 5pt;
    margin-left: auto;
    margin-right: auto;
}

div.analyzer {
    text-align: center;
    padding: 15pt;
    border-bottom: gray dotted thin;
}

img.analyzer {
    width: 30vw;
}

div.post {
    padding: 15pt;
    text-align: center;
    border: gray dotted thin;
}

img.post {
    width: 20vw;
}

p.post-caption {
    width: 20vw;
    text-align: justify;
}

p.post-user {
    width: 20vw;
    font-weight: bold;
}

div.select {
    border-radius: 50%;
    cursor: pointer;
    user-select: none;
    font-size: 3vw;
    margin-left: auto;
    margin-right: auto;
    width: 5vw;
    height: 5vw;
    background-color: lightgrey;
}

div.select-active {
    border-radius: 50%;
    cursor: pointer;
    user-select: none;
    font-size: 3vw;
    margin-left: auto;
    margin-right: auto;
    width: 5vw;
    height: 5vw;
    background-color: lightskyblue;
}